/* VARIABLES */
:root {
  --primary-color: lightgray; 
  --link-color: black;
}

html {
height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
  background: url(https://cdn.wallpapersafari.com/63/62/qmLtuM.jpg);
  background-position: center;
  background-size: cover;  
}



a {
    color: var(--link-color);
}

.nav-active {
    color: #FF00FF;
    background: green;
}
.mainContent {
    margin-top: 5%;
}
    
h1 {
    padding-bottom: 20px;
}
    
.table thead {
    background: var(--primary-color);
    font-weight: bold;
}

.btn-basic{
    background: var(--primary-color);
    
}

.booklist > h2 {
  font: 400 40px/1.5 Helvetica, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  border-bottom: 3px solid black;
}
 
.booklist > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
 
.booklist > ul > li {
  font: 200 20px/1.5 Helvetica, Verdana, sans-serif;
  border-bottom: 1px solid #ccc;
}
 
.booklist > ul > li:last-child {
  border: none;
}
 
.booklist > ul > li a {
  text-decoration: none;
  color: #000;
  display: block;
  width: 400px;
  transition: font-size 0.3s ease, background-color 0.3s ease;
}
 
.booklist > ul > li a:hover {
  font-size: 30px;
  background: #f6f6f6;
}

.bookadd {
	margin-left: auto;
}
    
    
.backart {
	background: linear-gradient(to left, rgb(7, 27, 82,0.5) 0%, rgba(0, 128, 128, 1) 100%);
	border-radius: 10px;
	color: #fff;
}
