.buttonSub:hover {background: linear-gradient(to left, rgba(0, 128, 128, 1) 0%, rgb(7, 27, 82,0.5) 100%);}
.inputTy {border: 1px solid #fff;}
.inputTy:hover {border: 1px solid rgb(7, 27, 82,0.5);}

